/* create some rules for ms teams dark mode, this is a first pass, will likely need several dozen more lines of code 
for different kendo widgets  */

// Kendo Grids for Teams
.hsbody.hsteams.v-contrast,
.hsbody.hsteams.v-dark {
    .k-grid {
        background-color: $hsteam-dark-bg2;
        color: $hsteam-dark-grid-row-color;

        .k-grid-header {
            background-color: $hsteam-dark-bg3;
            border-color: $hsteam-dark-border;
            color: $hsteam-dark-text1;

            tr th {
                border-bottom: 2px solid $hsteam-dark-border;
            }
        }

        .k-grid-content {

            tr,
            tr.k-alt {
                background-color: $hsteam-dark-grid-row;
                color: $hsteam-dark-grid-row-color;

                td {
                    border-bottom: 1px solid $hsteam-dark-border;
                }
            }

            tr.k-state-selected,
            tr:hover {
                background-color: $hsteam-dark-grid-row-hover;
                color: $hsteam-dark-text1;
            }
        }
    }
}

// End Kendo Grids for Teams

// Kendo Buttons for Teams 
.hsbody.hsteams.v-default,
.hsbody.hsteams {
    .k-button {
        background-color: $hsteam-brand;
        border-color: $hsteam-default-border;
        color: $hsteam-default-bg1;

        &:hover {
            background-color: $hsteam-brand-hover;
            color: $hsteam-default-bg1;
        }
    }
}

.hsbody.hsteams.v-contrast,
.hsbody.hsteams.v-dark {
    .k-button {
        border-color: $hsteam-dark-border;
    }
}

// End Kendo Buttons for Teams 

// Tile Color scheme adjustments for Teams
.hsbody.hsteams {
    .hs-teams-tile {
        @extend .hs-tile;
        background-color: $hsteam-brand !important;
        color: $hsteam-default-bg1 !important;

        >.hs-action-metric,
        .hs-action-icon,
        .hs-action-title {
            color: $hsteam-default-bg1 !important;
        }


        &:hover {
            background-color: lighten($hsteam-brand, 5%) !important;
        }

        & a {
            color: $hsteam-default-bg1 !important;
        }
    }
}

// End Tile Color scheme adjustments for Teams

.hsbody.hsteams.v-default,
.hsbody.hsteams {

    background-color: $hsteam-default-bg1;

    .hs-teams-text {
        color: $hsteam-default-text1;
    }

    .hs-teams-bg-color {
        background-color: $hsteam-default-bg1;

        .k-content {
            background-color: $hsteam-default-bg1;
        }
    }

    .k-card {
        background-color: $hsteam-default-card-bg;
        color: $hsteam-default-text1;
        border-color: $hsteam-default-border;

        .k-card-header {
            border-color: $hsteam-default-border;
            background-color: $hsteam-default-card-bg;
            border-bottom-width: 0;
        }

        .k-card-title {
            color: $hsteam-default-text1;
            font-size: $hsteam-font-size-title;
            font-weight: 700;

        }

        .k-card-subtitle {
            font-size: $hsteam-font-size-subtitle;
            color: $hsteam-default-text2;
        }
    }

    .k-tabstrip-wrapper {
        min-height: 1.8rem;
        background-color: $hsteam-default-bg1;

        .k-content.k-state-active {
            background-color: $hsteam-default-bg1;
        }

        .k-tabstrip-items {
            font-family: "Segoe UI", system-ui, "Apple Color Emoji", "Segoe UI Emoji", sans-serif;
            border: none;
            margin-bottom: 0;

            .k-item {
                &.k-first {
                    margin-left: 0;
                }

                &.k-last {
                    margin-right: 0;
                }

                font-weight: 400;
                font-size: 14px;
                height: 2.6rem;
                padding: 0;
                margin:0 6px;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                border: 0;
                border-radius: 0;
                max-width: 20rem;

                .k-link {
                    color: $hsteam-default-tab;
                    padding-left: 0 !important;
                    padding-right: 0 !important;
                }

                &.k-state-active {
                    .k-link {
                        color: $hsteam-default-tab-active;
                        font-weight: 600;
                    }

                    background-color:$hsteam-default-bg1;
                    border-bottom:3px solid $hsteam-default-tab-border-active;

                    &:hover {
                        border-bottom: 3px solid $hsteam-default-tab-border-active;
                    }
                }

                &:hover {
                    border-bottom: 3px solid $hsteam-tab-border;
                }
            }
        }
    }
}

.hsbody.hsteams.v-contrast,
.hsbody.hsteams.v-dark {
    background-color: $hsteam-dark-bg1;

    .hs-teams-text {
        color: $hsteam-default-bg1;
    }

    .hs-teams-bg-color {
        background-color: $hsteam-dark-bg1;

        .k-content {
            background-color: $hsteam-dark-bg1;
        }
    }

    .k-card {
        background-color: $hsteam-dark-card-bg;
        color: $hsteam-dark-text1;
        border-color: $hsteam-dark-border;

        .k-card-header {
            background-color: $hsteam-dark-card-bg;
            border-color: $hsteam-dark-border;
        }

        .k-card-title {
            color: $hsteam-dark-text1;
        }

        .k-card-subtitle {
            color: $hsteam-dark-text2;
        }
    }

    .k-tabstrip-wrapper {
        background-color: $hsteam-dark-bg1;

        .k-content.k-state-active {
            background-color: $hsteam-dark-bg1;
        }

        .k-tabstrip-items {
            .k-item {
                .k-link {
                    color: $hsteam-dark-tab;
                }

                &.k-state-active {
                    .k-link {
                        color: $hsteam-dark-tab-active;
                    }

                    background-color: $hsteam-dark-bg1;
                    border-bottom:3px solid $hsteam-dark-tab-border-active;

                    &:hover {
                        border-bottom: 3px solid $hsteam-dark-tab-border-active;
                    }
                }
            }
        }

        .k-content {
            background-color: $hsteam-dark-card-bg;
            border-color: $hsteam-dark-border;
            color: $hsteam-dark-text1;
        }
    }

    // make all the bootstrap text variants the same color in dark/contrast mode 
    @each $color,
    $value in $theme-colors {
        .text-#{$color} {
            color: $hsteam-dark-text1 !important;
        }
    }
}

.hsbody.hsteams.v-dark .k-card .k-card-title {
    color: $hsteam-dark-text1;
}

// ListView Corrections and MS Teams 
.hsbody.hsteams,
.hsbody-hsteams-v-default {
    .k-widget.k-listview {
        background-color: $hsteam-default-card-bg;

        .k-listview-content {
            background-color: $hsteam-default-card-bg;
            color: $hsteam-default-text1;

            &.list-group {
                .list-group-item {
                    background-color: $hsteam-default-bg1;

                    &:hover {
                        background-color: darken($hsteam-default-bg1, 5%);
                    }
                }
            }
        }
    }
}

.hsbody.hsteams.v-contrast,
.hsbody.hsteams.v-dark {
    .k-widget.k-listview {
        background-color: $hsteam-dark-card-bg;

        .k-listview-content {
            background-color: $hsteam-dark-card-bg;
            color: $hsteam-dark-text1;

            &.list-group {
                .list-group-item {
                    background-color: $hsteam-dark-bg2;
                    color: $hsteam-dark-text2;

                    &.list-group-item-danger,
                    &.hs-danger {
                        background-color: $hsteam-dark-danger;
                        color: $white;

                        &:hover {
                            background-color: lighten($hsteam-dark-danger, 10%);
                        }
                    }

                    &:hover {
                        background-color: $hsteam-dark-bg3;
                        color: $hsteam-dark-text1;
                    }
                }
            }
        }
    }
}

// END ListView Corrections and MS Teams 

// Dark Mode Adjustments for Kendo Scheduler 
.hsbody.hsteams.v-contrast,
.hsbody.hsteams.v-dark {
    .k-scheduler {
        background-color: $hsteam-dark-bg2;
        color: $hsteam-dark-grid-row-color;
        border-color: $hsteam-dark-border;

        .k-scheduler-toolbar.k-toolbar {
            background-color: $hsteam-dark-bg3;
            border-color: $hsteam-dark-border;
            color: $hsteam-dark-text1;
        }

        .k-scheduler-layout,
        .k-scheduler-content {

            &,
            * {
                background-color: $hsteam-dark-bg2;
                color: $hsteam-dark-grid-row-color;
                border-color: $hsteam-dark-border;
            }

            .k-other-month {

                &,
                * {
                    background-color: lighten($hsteam-dark-bg2, 10%) !important;
                }
            }

            .k-scheduler-table~[role=gridcell].k-event {

                &,
                * {
                    background-color: transparent !important;
                }
            }
        }

        .k-scheduler-content tr.k-state-hover td:not(.k-scheduler-datecolumn) {

            &,
            * {
                background-color: $hsteam-dark-grid-row-hover !important;
                color: $hsteam-dark-text1 !important;

            }
        }

        .k-more-events.k-button {

            &,
            * {
                background-color: $hsteam-brand;
                border-color: $hsteam-dark-border;
                color: $hsteam-default-bg1;
            }

            &:hover {
                background-color: $hsteam-brand-hover;
                color: $hsteam-default-bg1;
            }
        }

        .k-scheduler-table.k-scheduler-header-all-day~.k-event * {
            background-color: $hsteam-dark-bg3 !important;
            margin: $cell-padding-y $cell-padding-x;
        }
    }
}